.work-page{
    color: white;
    font-family: 'Montserrat', sans-serif;
}

.card-text{
    color: gray;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
}

.work-page-top{
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    margin-top: 130px;
    width: 70%;
}

.work-page-bottom{
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    margin-top: 40px;
    width: 70%;
}

.work-link{
    color: rgb(6, 217, 158);
    text-decoration: none;
}

.work-link:hover{
    text-decoration: underline;
}

Card.Img{
    height: 100px;
}

.card-title{
    font-size: 20px;
}

.work-page-top, .work-page-bottom {
    animation: fadeIn linear 1s;
    -webkit-animation: fadeIn linear 1s;
    -moz-animation: fadeIn linear 1s;
    -o-animation: fadeIn linear 1s;
    -ms-animation: fadeIn linear 1s;
}

.not-work{
    color:#FC6D6D;
    text-decoration: line-through;
}

.not-work:hover {
    text-decoration: line-through;
}

@media screen and (max-width: 900px) {
    .card{
        width: 15rem;
    }

    .card-title{
        font-size: 15px;
    }

    .card-text{
        font-size: 10px;
    }

    .card-logo{
        height: 150px;
    }
}

@media screen and (max-width: 700px) {
    .card{
        width: 15rem;
    }

    .card-title{
        font-size: 15px;
    }

    .card-text{
        font-size: 10px;
    }

    .card-logo{
        height: 100px;
    }
}